.burg {
	display: block;
	height: 0.18em;
	width: 28px;
	margin: 29px 0 28px 5px;
	background-color: #000;
	position: relative;
	-moz-transition: 0.2s;
	-o-transition: 0.2s;
	-webkit-transition: 0.2s;
	transition: 0.2s;
}
.burg:before {
	content: '';
	position: absolute;
	top: -8px;
	width: 28px;
	height: 0.18em;
	background-color: #000;
	-moz-transition: 0.2s;
	-o-transition: 0.2s;
	-webkit-transition: 0.2s;
	transition: 0.2s;
}
.burg:after {
	content: '';
	position: absolute;
	top: 8px;
	width: 28px;
	height: 0.18em;
	background-color: #000;
	-moz-transition: 0.2s;
	-o-transition: 0.2s;
	-webkit-transition: 0.2s;
	transition: 0.2s;
}
.activeBurg {
	background-color: transparent;
	-ms-transform-origin:50% 50%;
	-webkit-transform-origin:50% 50%;
	transform-origin:50% 50%;
}
.activeBurg:before {
	-ms-transform: rotate(45deg);
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
	top: 0;
}
.activeBurg:after {
	-ms-transform: rotate(-45deg);
	-webkit-transform: rotate(-45deg);
	transform: rotate(-45deg);
	top: 0;
}

