/*
--------------------------
07. FULLSCREEN VIDEOBG
--------------------------
*/

.section-video-container{
	height:450px;
}
.video-container{
	height:100%;
	position:absolute; 
	top:0;
	bottom:0;
	left:0;
	right:0;
}

.player-background{
	position:absolute; 
	top:0;
	bottom:0;
	left:0;
	right:0;
	background-image:url('../img/player_fallback.jpg');
	background-position: center;
	background-size: cover;
}
.section-video-controls{
	display:table;
	width:100%;
	height:100%;
	border:0;
	outline:0;
	position:absolute;
	top:50%;
	bottom:0;
	right: 0;
	left: 0;
	margin-top:-32px;
	opacity:0.5;
	text-align:center;
	z-index:2;
}

.section-video-controls .command:before{
	font-family:"Ionicons";
	display:block;
	color:#f0f0f0;
	line-height:1;
}
.section-video-controls .command-play{
	display:none;
}
.section-video-controls .command-play:before{
	content:"\f487";
}
.section-video-controls .command-pause:before{
	content:"\f477";
}


/*
--------------------------
08. OWL CAROUSEL
--------------------------
*/
.owl-carousel{
	overflow:hidden;
}
.owl-controls{
	display:block;
	margin-top:30px;
	text-align:center;
}
.owl-dot {
	display:inline-block;
}
.owl-dot span{
	display:block;
	width:10px;
	height:10px;
	border-radius:50%;
	background-color:#d5d5d5;
	opacity:0.5;
	margin:0 10px;
}
.owl-dot.active span{
	background-color:#000;
	opacity:1;
}
.custom-slider{
	margin-bottom:70px;
}
.custom-slider .owl-controls{
	width:100%;
	position:absolute;
	top:50%;
	margin-top:0;
}

.owl-nav i{
	color:#000;
}
.owl-prev, .owl-next{
	display:inline-block;
	position:absolute;
	margin-top:-25px;
	background-color:#fff;
	padding:11px 18px;
	line-height: 1;
	transition:all ease 0.2s;
	-webkit-transition:all ease 0.2s;
	opacity:0.5;
}
.owl-prev{
	left:-60px;
}

.owl-next{
	right:-60px;
}

.owl-carousel:hover .owl-prev{
	left:0;
}
.owl-carousel:hover .owl-next{
	right:0;
}
.owl-prev:hover, .owl-next:hover{
	opacity:1;
}

