.heading{
    margin-top: 0px;
}

.heading-underlined:after{
	display: block;
    content: "";
    border-bottom: 1px solid;
    border-color: #d5d5d5;
    width: 45px;
    margin-top: 18px;
}
.section-heading .heading-underlined:after{
	width:60px;
	margin:25px 0;
}
.text-left .heading-underlined:after{
	margin-left: 0;
}
.text-center .heading-underlined:after{
    margin-left: auto;
	margin-right: auto;
}
.text-right .heading-underlined:after{
	margin-right: 0;
	margin-left: auto;
}
.heading-underlined.black:after{
    border-color: #000;
}
.heading-crossed{
	position:relative;
	z-index:1;
}
.heading-crossed:after{	
	display: block;
	content: "";
	position: absolute;
	border-bottom: 1px solid;
	border-color: #d5d5d5;
	width: 200px;
	margin-bottom:-1px;
	-webkit-transform:rotate(-45deg);
	-ms-transform:rotate(-45deg);
	transform:rotate(-45deg);
	z-index: -1;
	bottom: 50%;
	margin-left: 0;
	left:auto;
	opacity:0.6;
}
.text-left .heading-crossed:after{
	margin-left: 0;
	left:auto;
}
.text-center .heading-crossed:after{
	left: 50%;
	margin-left: -100px;
}
.text-right .heading-crossed:after{
	left: auto;
	right: 0px;
}
.heading-strike{
	display:inline-block;
	position: relative;
	z-index: 1;
	margin-bottom: 1em;
}
.heading-strike:before{
	content: "";
	border-bottom: 1px solid;
	border-color: #d5d5d5;
	position: absolute;
	display: block;
	z-index: -1;
	width: 120%;
	margin: -1px 0 0 -10%;
	top: 50%;
	opacity:0.6;
}
.heading-strike:after{
	display: block;
	content: "";
	border-bottom: 5px solid;
	width: 26px;
	margin: 15px auto 25px;
	z-index: -1;
	position: absolute;
	left: auto;
	-ms-transform: none;
	-webkit-transform: none;
	transform: none;	
}
.text-left .heading-strike:after{
	left: auto;
	-ms-transform: none;
	-webkit-transform: none;
	transform: none;
}
.text-center .heading-strike:after{
	left: 50%;
	-ms-transform: translateX(-13px);
	-webkit-transform: translateX(-13px);
	transform: translateX(-13px);
}
.text-right .heading-strike:after{
	right: 0px;
	left:auto;
	-ms-transform: none;
	-webkit-transform: none;
	transform: none;
}

.heading-has-icon:before{
	font-size:24px;
